@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
     -moz-text-size-adjust: none;
          text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

html {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #a8e0e3;
  font-family: "Space Mono", mono;
  height: 100vh;
  width: 100vw;
  display: flex;
}
html .App {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: auto;
  width: 64rem;
  height: 40rem;
}
html .App h1 {
  color: #345d5d;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  width: 16rem;
  margin: auto;
  margin-bottom: 3rem;
  margin-top: 3rem;
}
html .App .container {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  background-color: white;
  margin-top: auto;
  height: 32rem;
  border-radius: 2rem;
}
html .App .container .calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-evenly;
  margin-right: 1rem;
  width: 50%;
  border-radius: 2rem;
  margin-left: 2rem;
  text-align: left;
  font-family: "Space Mono", mono;
}
html .App .container .calculator input {
  height: 3rem;
  font-size: 1.75rem;
  border: none;
  background-color: #f4fafa;
  border-radius: 0.5rem;
  text-align: right;
  padding-right: 1rem;
  -moz-appearance: textfield;
  font-family: "Space Mono", mono;
  color: #5e7a7d;
}
html .App .container .calculator input input[type=number]::-webkit-outer-spin-button,
html .App .container .calculator input input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
html .App .container .calculator input:focus {
  outline: 2px solid green;
}
html .App .container .calculator .bill-input {
  background: no-repeat url(/tipping-app/static/media/icon-dollar.9f9be353.svg);
  background-size: 2rem 85%;
}
html .App .container .calculator .split-input {
  background: no-repeat url(/tipping-app/static/media/icon-person.65bfd204.svg);
  background-size: 2rem 85%;
}
html .App .container .calculator .input-title {
  display: flex;
  flex-direction: row;
}
html .App .container .calculator p {
  margin-bottom: 0.75rem;
  padding-top: 1rem;
  color: #6f7a7d;
}
html .App .container .calculator .warning {
  color: red;
  margin-left: auto;
  flex-wrap: nowrap;
  font-size: 0.85rem;
}
html .App .container .calculator .tip-buttons {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 0.75rem;
  gap: 0.75rem;
}
html .App .container .calculator .tip-buttons input {
  border: none;
  font-family: "Space Mono", mono;
  background-color: #1b464a;
  color: white;
  width: 7.5rem;
  height: 3rem;
  margin: 0.45rem;
  text-align: center;
  font-size: 1.75rem;
  padding-right: 0rem;
}
html .App .container .calculator .tip-buttons input:hover {
  background-color: #ade7df;
  color: #1b464a;
}
html .App .container .calculator .tip-buttons input[type=number] {
  text-align: right;
  padding-right: 1rem;
}
html .App .container .display {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2rem;
  margin-left: 1rem;
  width: 50%;
  background-color: #1b464a;
  border-radius: 2rem;
  padding-top: 1rem;
}
html .App .container .display .total {
  display: flex;
  flex-direction: row;
  color: white;
  margin: 2rem 2rem;
  width: 90%;
}
html .App .container .display .total .info {
  flex-direction: column;
  margin-right: auto;
  margin-bottom: auto;
  line-height: 1.5;
  margin-left: 1.5rem;
}
html .App .container .display .total .info .primary {
  font-size: 1rem;
  text-align: left;
  font-weight: bold;
}
html .App .container .display .total .info .secondary {
  font-size: 1rem;
  text-align: left;
}
html .App .container .display .total .cost {
  margin-left: auto;
  margin-top: auto;
  font-size: 5rem;
  color: #5dbeac;
}
html .App .container .display input {
  display: inline-block;
  padding-bottom: 0.125rem;
  width: 80%;
  margin-top: auto;
  margin-bottom: 2.5rem;
  font-family: "Space Mono", mono;
  font-size: 1.5rem;
  height: 3rem;
  background-color: #2e666c;
  color: #1b464a;
  border: none;
  border-radius: 0.5rem;
  font-weight: bold;
}
html .App .container .display input:hover {
  background-color: #ade6df;
  color: #1a4240;
}
html #custom {
  background-color: #f4fafa;
  color: #1b464a;
  text-align: right;
  width: 7.5rem;
  height: 3rem;
}
html #custom::-webkit-input-placeholder {
  text-align: center;
  padding: 0rem;
}
html #custom:-ms-input-placeholder {
  text-align: center;
  padding: 0rem;
}
html #custom::placeholder {
  text-align: center;
  padding: 0rem;
}

@media only screen and (max-width: 420px) {
  html .App {
    width: 100vw;
  }
  html .App .container {
    flex-direction: column;
    height: 120rem;
  }
  html .App .container .calculator {
    width: 90%;
  }
  html .App .container .calculator .tip-buttons {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(2, 1fr);
    width: 90%;
  }
  html .App .container .display {
    width: 90%;
  }
  html .App .container .display .total .cost {
    font-size: 4rem;
  }
}

